				<view class="swiper-item uni-bg1">
					<view class="header">
						<view class="status_bar"><!-- 这里是状态栏 --></view>
						<view class="header_cont">
							<view class="logo"></view>
						</view>
					</view>
					<view class="tit"></view>
					<view class="tit1">
						<text class="text">立即观看</text>
						<text class="icon"></text>
						<text class="text">你的故事</text>
					</view>
					<view class="desc">
						<text>只要你玩过有戏</text>
