						<text>而我们去发现无限可能</text>
					</view>
					<view class="swipers">
						<swiper  class="swiper-tall" :indicator-dots="indicatorDots2" :autoplay="autoplay2" :previous-margin="previousMargin"
						 :next-margin="nextMargin" :circular="circular" @change="change" :current="swiperCurrentIndex">
							<swiper-item class="swiper-container" v-for="(img,index) in imgs" :key="index"
							 :item-id="index" :data-year="index">
							 
							<view class="swiper-item"
							  :style="{background:img?('url('+ img +') center no-repeat'):'',backgroundSize:'100% 100%'}" 
							   :animation="animationData[index]">
							   <image :src="img" mode="" class="swiper_ing"></image>
