					</view>
				</view>
			</swiper-item>
			
		</swiper>
		<TabBar tabIndex=0></TabBar>
	</view>
</template>

<script>
	import TabBar from '../../components/jinjie-tabBar/jinjie-tabBar.vue'  
	export default {
		
		data() {
			return {
				background: ['color1', 'color2', 'color3'],
				indicatorDots: false,
				autoplay: false,
				interval: 2000,
				duration: 500,
				screenHeight: 0,
				animationData: {
					0:{},
					1:{},
					2:{}
				},
				title: '0',
				indicatorDots2: false,
				autoplay2: false,
				previousMargin: uni.upx2px(185)+'px',
				nextMargin: uni.upx2px(185)+'px',
				circular: true,
				zoomParam: 1.20,
				swiperCurrentIndex: 0,
				data: [],
				max: 0,
				imgs:[
					"http://radio.newfeeling.cn/WechatIMG259.png",
					"http://radio.newfeeling.cn/WechatIMG259.png",
					"http://radio.newfeeling.cn/WechatIMG259.png",
				]
			};
		},
		components: {
			TabBar,
		},
		computed:{
			
		},
		onLoad() {
			this.animation = uni.createAnimation();
			this.animation.scale(this.zoomParam).step();
			this.animationData[0] = this.animation.export();
		},
		
		methods: {
			toUrl(){
				uni.navigateTo({
					url:"./play/play"
				})
			},
			change(e) {
				this.swiperCurrentIndex = e.detail.current;
				this.title = e.detail.currentItemId;
				for (let key in this.animationData) {
					if (e.detail.currentItemId == key) {
						this.animation.scale(this.zoomParam).step();
						this.animationData[key] = this.animation.export();
					} else {
						this.animation.scale(1.0).step();
						this.animationData[key] = this.animation.export();
					}
				}
			}
		}
	}
</script>

